.main-container{
    display: flex;
    flex-direction: column;
    max-width: 500px;
    border-radius: 10px;
    background-color: rgba(235, 225, 225, 0.3);
    padding: 20px;
    margin: auto;
    margin-top: 20px;
    margin-bottom:50px;
   
}
.left-quotes-sign{
    width:50px;
}
.phrase-container{
    display: flex;
    font-weight: 500;
    font-size: 30px;
    font-style: italic;
    justify-content: flex-start;
    margin-bottom: 20px;
}
.data{
    font-size: 20px;
    font-weight: 400;
    
}
.image-name-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}
.woman-image-container{
    height: 100px;
    width: 100px;
    border-radius: 5px;
    object-fit: cover;
    overflow: hidden;
}
.woman-image-phrase{
    object-fit: cover;
    height: 100%;
}
.woman-name-phrase{
    font-size: 20px;
    margin: 10px 0;
}
