@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:ital,wght@0,400;0,600;1,500&display=swap');
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: 'Barlow Condensed', sans-serif;
  text-align: center;
  background: rgb(124,181,94);
  background: linear-gradient(180deg, rgba(124,181,94,1) 0%, rgba(67,17,98,1) 100%);
  min-height: 100vh;
  width: 100vw;
}
.main-title{
  text-transform: uppercase;
  display: inline-block;
  font-size: 60px;
  margin-bottom: 10px;
  color:#ebebeb;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
